import React, { useState } from "react";
import "./MenuBjx.css";

import Bagues from "./OngletsBijoux/bagues/Bagues";
import Boucles from "./OngletsBijoux/boucles/Boucles";
import Bracelets from "./OngletsBijoux/bracelets/Bracelets";
import Colliers from "./OngletsBijoux/colliers/Colliers";
import Pendentifs from "./OngletsBijoux/pendentifs/Pendentifs";

export default function MenuBjx() {
  const [stateMenuBjx, setStateMenuBjx] = useState(1);

  const go1 = () => {
    setStateMenuBjx(1);
  };

  const go2 = () => {
    setStateMenuBjx(2);
  };

  const go3 = () => {
    setStateMenuBjx(3);
  };

  const go4 = () => {
    setStateMenuBjx(4);
  };

  const go5 = () => {
    setStateMenuBjx(5);
  };

  return (
            
    <div className="containBjx">
      <div className="contBtnBjx">
        <div
          onClick={go1}
          id={"bracelets"}
          className={`MenuBjx ${stateMenuBjx === 1 ? "active" : ""}`}
        >
          <h1 className="buttonBjx">Bracelets et manchettes</h1>
        </div>

        <div
          onClick={go2}
          id={"bagues"}
          className={`MenuBjx ${stateMenuBjx === 2 ? "active" : ""}`}
        >
          <h1 className="buttonBjx">Bagues</h1>
        </div>
        <div
          onClick={go3}
          id={"boucles"}
          className={`MenuBjx ${stateMenuBjx === 3 ? "active" : ""}`}
        >
          <h1 className="buttonBjx">Boucles d'oreilles</h1>
        </div>

        <div
          onClick={go4}
          id={"colliers"}
          className={`MenuBjx ${stateMenuBjx === 4 ? "active" : ""}`}
        >
          <h1 className="buttonBjx">Colliers</h1>
        </div>

        <div
          onClick={go5}
          id={"pendentifs"}
          className={`MenuBjx ${stateMenuBjx === 5 ? "active" : ""}`}
        >
          <h1 className="buttonBjx">Pendentifs</h1>
        </div>
      </div>

      <div className="containerBjx">
        {stateMenuBjx === 1 ? (
          <div className="contBjx">
            <section className="bracelets">
              <Bracelets />
            </section>
          </div>
        ) : stateMenuBjx === 2 ? (
          <div className="contBjx">
            <section className="bagues">
              <Bagues />
            </section>
          </div>
        ) : stateMenuBjx === 3 ? (
          <div className="contBjx">
            <section className="boucles">
              <Boucles />
            </section>
          </div>
        ) : stateMenuBjx === 4 ? (
          <div className="contBjx">
            <section className="colliers">
              <Colliers />
            </section>
          </div>
        ) : (
          <div className="contBjx">
            <section className="pendentifs">
              <Pendentifs />
            </section>
          </div>
        )}
      </div>
    </div>
  );
}
