import React, { useState } from "react";

import SlideshowColliers from "./slides/SlideColliers";
import SlideshowMariage from "./slides/SlideMariage";
import SlideshowColliersLyto from "./slides/SlideColliersLyto";

import productsColliers from "./products/ProductsColliers";
import productsMariage from "./products/ProductsMariage";
import productsColliersLyto from "./products/ProductsColliersLyto";

export default function OngletsColliers() {
  const [stateOnglets, setStateOnglets] = useState(1);

  const goColliers = () => {
    setStateOnglets(1);
  };

  const goColliersMariage = () => {
    setStateOnglets(2);
  };

  const goColliersLyto = () => {
    setStateOnglets(3);
  };

  return (
    <div className="contain">
      <div className="contBtn">
        <div
          onClick={goColliers}
          className={`onglets ${
            stateOnglets === 1 ? "active" : ""
          }`}
        >
          {" "}
          <h1>Colliers en fil d'aluminium.</h1>
          
        </div>
        <div
          onClick={goColliersMariage}
          className={`onglets ${
            stateOnglets === 2 ? "active" : ""
          }`}
        >
          <h1>Colliers spécial mariage.</h1>
          
        </div>
        <div
          onClick={goColliersLyto}
          className={`onglets ${
            stateOnglets === 3 ? "active" : ""
          }`}
        >
          <h1>Colliers lytothérapie.</h1>
          
        </div>
      </div>
      <div className="container">
        {stateOnglets === 1 ? (
          <div className="cont">
            <section className="SlideshowColliers">
              {productsColliers.map(product => (
                <SlideshowColliers
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) : stateOnglets === 2 ? (
          <div className="cont">
            <section className="SlideshowMariage">
              {productsMariage.map(product => (
                <SlideshowMariage
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        ) : (
          <div className="cont">
            <section className="SlideshowColliersLyto">
              {productsColliersLyto.map(product => (
                <SlideshowColliersLyto
                  title={product.title}
                  price={product.price}
                  description={product.description}
                  link={product.link}
                  reference={product.reference}
                  images={product.images}
                />
              ))}
            </section>
          </div>
        )}
      </div>
    </div>
  );
}
